import BoardFooter from './components/BoardFooter.vue'
import SiteMenu from './components/SiteMenu.vue'
export default {
  name: "APP",
  components: {
    'BoardHead' : BoardHead,
    'BoardFooter' : BoardFooter,
    'SiteMenu' : SiteMenu
  }
}
</script>

<style>
  body {
    text-align: center;
    background-color: #F6F6F8;
    height: 100%;
  }
  input {
    border-style: groove;
    width:200px;
  }
  button {
    border-style: groove;
  }
</style>



