<template>
    <div class="container">
        <form class="form-horizontal" @submit.prevent="checkForm" action="http://localhost:3000/api/board/post" method="post">
            <div class="form-group">
                <label for="subject" class="col-sm-2 control-label">제목</label>
                <div class="col-sm-10">
                    <input type="text" v-model="subject" class="form-control" name="subeject" id="subject" placeholder="제목을 입력하세요.">
                </div>
            </div>
            <div class="form-group">
                <label for="contents" class="col-sm-2 control-label">내용</label>
                <div class="col-sm-10">
                    <textarea v-model="contents" class="form-control" name="contents" id="contents" placeholder="내용을 입력하세요." rows="5"/>
                </div>
            </div>
            <div class="form-group">
                <label for="writer" class="col-sm-2 control-label">작성자</label>
                <div class="col-sm-10">
                    <input type="text" v-model="writer" class="form-control" name="writer" id="writer" placeholder="작성자를 입력하세요.">
                </div>
            </div>
            <div class="form-group">
                <label for="password" class="col-sm-2 control-label">비밀번호</label>
                <div class="col-sm-10">
                    <input type="password" v-model=password class="form-control" name="password" id="password" placeholder="비밀번호를 입력하세요.">
                </div>
            </div>
            <div class="pull-right">
                <button type="submit" class="btn btn-success">작성</button>
                <router-link to="/api/board" class="btn btn-default">취소</router-link>
            </div>
        </form>
    </div>
</template>

<script>
export default {
    data: function() {
        return {
            subject: null,
            contents: null,
            writer: null,
            password: null
        }
    },
    methods: {
        checkForm: function() {
            if(!this.subject) {
                alert('제목을 입력해주세요.');
                return false;
            }
            if(!this.contents) {
                alert('내용을 입력해주세요.');
                return false;
            }
            if(!this.writer) {
                alert('작성자를 입력해주세요.');
                return false;
            }
            if(!this.password) {
                alert('비밀번호를 입력해주세요.');
                return false;
            }
            //http 통신
            
        }
    }
}
</script>

<style>

</style>


