<template>
  <div class="container">
      <div class="tablezone">
        <table class="table table-striped">
            <thead>
                <tr>
                    <th style="width:8%">번호</th>
                    <th style="width:50%">제목</th>
                    <th style="width:12%">작성자</th>
                    <th style="width:9%">파일</th>
                    <th>등록일</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td colspan="5">등록된 게시물이 없습니다.</td>
                </tr>
            </tbody>
        </table>
      </div>
      <div class="pull-right">
        <router-link to="/api/board/post" class="btn btn-success">글쓰기</router-link>
      </div>
  </div>
</template>

<script>
export default {
    data: function() {
        return {
            articles: []
        };
    },
    methods: {
        fetchArticles: function() {
            axios.get('http://localhost:3000/api/board').then((response) => {
                this.articles = response.data
            }, (error) => {
                console.log(error)
            })
        }
    },
    mounted: function() {
        this.fetchArticles()
    }
}
</script>

<style scoped>
    .tablezone {
        height: 300px;
    }
</style>


